
'use client';

import React, { useState, useEffect, useMemo } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { MapComponent } from '@/components/map';
import { LoaderCircle, School, MapPin, Home, AlertTriangle, ShieldAlert, CheckCircle2, Siren } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { getDirections, DirectionDetails } from '@/lib/directions-service';
import { savePoliceAlert } from '@/lib/police-alert-service';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogTrigger, DialogFooter } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Label } from '@/components/ui/label';

type Location = {
  lat: number;
  lng: number;
};

const alertFormSchema = z.object({
  childName: z.string().min(1, { message: "Child's name is required." }),
  schoolName: z.string().min(1, { message: "School's name is required." }),
  overdueDuration: z.string().min(1, { message: "This field is required." }),
  timeLeftSchool: z.string().min(1, { message: "This field is required." }),
  schoolContact: z.string().min(1, { message: "School contact info is required." }),
});

export default function ParentalAlertsPage() {
  const [homeLocation, setHomeLocation] = useState<Location | null>(null);
  const [schoolLocation, setSchoolLocation] = useState<Location | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isCalculatingRoute, setIsCalculatingRoute] = useState(false);
  const [routeDetails, setRouteDetails] = useState<DirectionDetails | null>(null);
  const [isAlertDialogOpen, setIsAlertDialogOpen] = useState(false);
  const [isSubmittingAlert, setIsSubmittingAlert] = useState(false);
  
  const { toast } = useToast();

  const form = useForm<z.infer<typeof alertFormSchema>>({
    resolver: zodResolver(alertFormSchema),
    defaultValues: {
      childName: '',
      schoolName: '',
      overdueDuration: '',
      timeLeftSchool: '',
      schoolContact: '',
    },
  });

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const loc = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };
          setHomeLocation(loc);
          setIsLoading(false);
        },
        (error) => {
          console.error("Error getting location:", error);
          toast({ variant: 'destructive', title: 'Could not get your location.' });
          setIsLoading(false);
        }
      );
    } else {
        setIsLoading(false);
    }
  }, [toast]);
  
  const handleMapClick = (e: google.maps.MapMouseEvent) => {
    if (e.latLng) {
      const newSchoolLocation = {
        lat: e.latLng.lat(),
        lng: e.latLng.lng(),
      };
      setSchoolLocation(newSchoolLocation);
    }
  };

  useEffect(() => {
    const calculateRoute = async () => {
      if (homeLocation && schoolLocation) {
        setIsCalculatingRoute(true);
        setRouteDetails(null);
        try {
          const result = await getDirections(homeLocation, schoolLocation);
          if (result.success && result.data) {
            setRouteDetails(result.data);
          } else {
             toast({ variant: 'destructive', title: 'Could not calculate route.', description: result.error });
          }
        } catch(e) {
          toast({ variant: 'destructive', title: 'An error occurred while calculating the route.' });
        } finally {
          setIsCalculatingRoute(false);
        }
      }
    };
    calculateRoute();
  }, [homeLocation, schoolLocation, toast])
  
  const mapCenter = useMemo(() => {
      if (schoolLocation) return schoolLocation;
      if (homeLocation) return homeLocation;
      return { lat: 37.7749, lng: -122.4194 };
  }, [homeLocation, schoolLocation])

  const mapPins = useMemo(() => {
    const pins = [];
    if(homeLocation) {
        pins.push({ id: 'home', location: { latitude: homeLocation.lat, longitude: homeLocation.lng }, category: 'Home', severity: 'Low', summary: 'Home', timestamp: ''})
    }
    if(schoolLocation) {
        pins.push({ id: 'school', location: { latitude: schoolLocation.lat, longitude: schoolLocation.lng }, category: 'School', severity: 'Low', summary: "School Location", timestamp: ''})
    }
    return pins;
  }, [homeLocation, schoolLocation])

  async function onAlertSubmit(values: z.infer<typeof alertFormSchema>) {
    if (!homeLocation || !schoolLocation) {
      toast({ variant: 'destructive', title: "Location data missing." });
      return;
    }
    setIsSubmittingAlert(true);
    try {
      const result = await savePoliceAlert({
        ...values,
        homeLocation,
        schoolLocation,
      });

      if (result.success) {
        toast({
          title: 'Police Alert Sent',
          description: `Your report has been filed with ID: ${result.id}`,
        });
        setIsAlertDialogOpen(false);
        form.reset();
      } else {
        toast({
          variant: 'destructive',
          title: 'Failed to Send Alert',
          description: result.error || 'An unknown error occurred.',
        });
      }
    } catch(e) {
      toast({
        variant: 'destructive',
        title: 'Submission Error',
        description: 'An error occurred while submitting the alert.',
      });
    } finally {
      setIsSubmittingAlert(false);
    }
  }
  
  const TrafficAlertCard = ({details}: {details: DirectionDetails}) => {
      let AlertIcon = CheckCircle2;
      let cardColor = 'bg-green-50 border-green-200';
      let textColor = 'text-green-800';
      let badgeColor = 'bg-green-500 hover:bg-green-500/80';
      let statusText = 'Light Traffic';
      
      if (details.trafficStatus === 'moderate') {
        AlertIcon = ShieldAlert;
        cardColor = 'bg-yellow-50 border-yellow-200';
        textColor = 'text-yellow-800';
        badgeColor = 'bg-yellow-500 hover:bg-yellow-500/80';
        statusText = 'Moderate Traffic';
      } else if (details.trafficStatus === 'heavy') {
        AlertIcon = AlertTriangle;
        cardColor = 'bg-red-50 border-red-200';
        textColor = 'text-red-800';
        badgeColor = 'bg-red-500 hover:bg-red-500/80';
        statusText = 'Heavy Traffic';
      }

      const showPoliceAlertButton = details.trafficStatus === 'moderate' || details.trafficStatus === 'heavy';

      return (
        <Card className={`shadow-lg animate-in fade-in-50 ${cardColor}`}>
            <CardHeader>
                <CardTitle className={`flex items-center gap-3 font-headline text-2xl ${textColor}`}>
                    <AlertIcon className="w-8 h-8" />
                    Traffic Alert for Your Route
                </CardTitle>
                <CardDescription>To your selected destination</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
                <div className='flex justify-around text-center'>
                    <div>
                        <p className='text-sm font-medium text-muted-foreground'>Est. Travel Time</p>
                        <p className={`text-3xl font-bold ${textColor}`}>{details.durationInTraffic}</p>
                    </div>
                    <div>
                        <p className='text-sm font-medium text-muted-foreground'>Distance</p>
                        <p className={`text-3xl font-bold ${textColor}`}>{details.distance}</p>
                    </div>
                </div>
                <div className='text-center'>
                    <Badge className={badgeColor}>{statusText}</Badge>
                </div>
                <p className="text-sm text-center text-muted-foreground pt-2">{details.summary}</p>

                {showPoliceAlertButton && (
                  <div className="pt-4 border-t border-current/20">
                    <Dialog open={isAlertDialogOpen} onOpenChange={setIsAlertDialogOpen}>
                        <DialogTrigger asChild>
                            <Button variant="destructive" className='w-full'>
                                <Siren className="mr-2 h-4 w-4" />
                                Alert Police
                            </Button>
                        </DialogTrigger>
                        <DialogContent className="sm:max-w-[480px]">
                            <DialogHeader>
                                <DialogTitle>Emergency Alert Details</DialogTitle>
                                <DialogDescription>
                                    Please provide the following information. This will be sent to the authorities.
                                </DialogDescription>
                            </DialogHeader>
                            <Form {...form}>
                                <form onSubmit={form.handleSubmit(onAlertSubmit)} className="space-y-4">
                                <FormField
                                    control={form.control}
                                    name="childName"
                                    render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Child's Full Name</FormLabel>
                                        <FormControl>
                                        <Input placeholder="e.g., Jane Doe" {...field} />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                    )}
                                />
                                <FormField
                                    control={form.control}
                                    name="schoolName"
                                    render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>School's Name</FormLabel>
                                        <FormControl>
                                        <Input placeholder="e.g., Springfield Elementary" {...field} />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                    )}
                                />
                                <div className="grid grid-cols-2 gap-4">
                                     <FormField
                                        control={form.control}
                                        name="overdueDuration"
                                        render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Time Overdue</FormLabel>
                                            <FormControl>
                                            <Input placeholder="e.g., 30 minutes" {...field} />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                        )}
                                    />
                                    <FormField
                                        control={form.control}
                                        name="timeLeftSchool"
                                        render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Time Left School</FormLabel>
                                            <FormControl>
                                            <Input placeholder="e.g., 3:30 PM" {...field} />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                        )}
                                    />
                                </div>
                                <FormField
                                    control={form.control}
                                    name="schoolContact"
                                    render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>School Contact Info (Phone/Email)</FormLabel>
                                        <FormControl>
                                        <Input placeholder="e.g., 555-123-4567" {...field} />
                                        </FormControl>
                                        <FormMessage />
                                    </FormItem>
                                    )}
                                />
                                <DialogFooter>
                                    <Button type="submit" variant="destructive" disabled={isSubmittingAlert} className='w-full'>
                                    {isSubmittingAlert ? (
                                        <><LoaderCircle className="mr-2 h-4 w-4 animate-spin" /> Submitting...</>
                                    ) : (
                                        "Submit Alert to Authorities"
                                    )}
                                    </Button>
                                </DialogFooter>
                                </form>
                            </Form>
                        </DialogContent>
                    </Dialog>
                  </div>
                )}
            </CardContent>
        </Card>
      )
  }

  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-8">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Parental Alerts</h1>
          <p className="mt-4 text-lg text-muted-foreground">Click the map to set a destination and check live traffic conditions.</p>
        </header>
        
        {isLoading && (
            <div className='flex justify-center items-center gap-2 text-muted-foreground'>
                <LoaderCircle className="w-5 h-5 animate-spin" />
                <p>Getting your current location...</p>
            </div>
        )}

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <div className="md:col-span-1 space-y-6">
             <Card>
                <CardHeader>
                    <CardTitle className='flex items-center gap-2'><Home className='w-5 h-5 text-primary' /> Your Location</CardTitle>
                </CardHeader>
                <CardContent>
                    {homeLocation ? (
                       <p className='text-muted-foreground text-sm font-mono'>
                           Lat: {homeLocation.lat.toFixed(5)}, Lng: {homeLocation.lng.toFixed(5)}
                       </p>
                    ) : (
                        <p className='text-muted-foreground text-sm'>Location not available.</p>
                    )}
                </CardContent>
             </Card>
             <Card>
                <CardHeader>
                    <CardTitle className='flex items-center gap-2'><School className='w-5 h-5 text-primary' /> Destination</CardTitle>
                    <CardDescription>Click on the map to set the destination location.</CardDescription>
                </CardHeader>
                <CardContent>
                    {schoolLocation ? (
                        <p className='text-muted-foreground text-sm font-mono'>
                           Lat: {schoolLocation.lat.toFixed(5)}, Lng: {schoolLocation.lng.toFixed(5)}
                       </p>
                    ) : (
                        <p className='text-muted-foreground text-sm'>No location selected yet.</p>
                    )}
                </CardContent>
             </Card>
          </div>
          <div className="md:col-span-2 space-y-6">
            <Card className="h-[450px]">
                 <MapComponent
                    incidents={mapPins}
                    onPinClick={() => {}}
                    onMapClick={handleMapClick}
                    center={mapCenter}
                    zoom={schoolLocation ? 14 : 12}
                    routePolyline={routeDetails?.polyline}
                    showTraffic={true}
                  />
            </Card>
            {isCalculatingRoute && (
                 <Card className="flex items-center justify-center p-8">
                    <LoaderCircle className="w-8 h-8 animate-spin text-primary mr-4" />
                    <p className="text-muted-foreground">Calculating best route and checking traffic...</p>
                 </Card>
            )}
            {routeDetails && <TrafficAlertCard details={routeDetails} />}
          </div>
        </div>
      </main>
    </div>
  );
}
